/**
 * 生成路由
 * @param {*} menuList 菜单列表
 */

export function generateRoutes(menuList) {
  if (!Array.isArray(menuList)) menuList = [];
  var menuUrlList = menuList.map(function (item) {
    return item.url;
  });

  var fn = function fn(arr) {
    var list = arr.filter(function (item) {
      // 公共页面，不受权限控制
      var meta = item.meta || {};
      if (meta.public) return true;
      var index = menuUrlList.indexOf(item.path); // 查找元素索引

      var res = index !== -1; // 是否存在

      res && Array.isArray(item.children) && item.children.length && (item.children = fn(item.children)); // 替换路由配置的meta.title

      if (res) item.meta.title = menuList[index].menuName;
      return res;
    });
    return list;
  };

  var tree = fn(asyncRoutes); // 添加404路由配置，因为*匹配需要放最后面。
  // tree.push(asyncRoutes[asyncRoutes.length - 1]);

  return tree;
}
