import Vue from 'vue';
import store from '../../store';
import config from '../../config';
/**
 * 是否有权限
 * @param {*} authConfig 权限配置
 */

export function has(authConfig) {
  // 不启用按钮权限功能
  if (!config.enabledButtonAuth) return true; // 无权限配置，不校验

  if (!authConfig || !authConfig.length) return true;
  var authList = store.getters && store.getters.authList || [];
  var hasPermission = authConfig.some(function (role) {
    return authList.indexOf(role) >= 0;
  });
  return hasPermission;
} // 挂在权限校验方法

Vue.has = has;
Vue.prototype.$has = has;